.container {
    background: #3853FF;
    display: flex;
    height: 56px;
    padding: 8px 16px;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    width: 100%;
    position: relative;
}

.left-area {
    display: flex;
    align-items: center;
    gap: 16px;
}

.menu {
    display: flex;
    align-items: center;
    gap: 24px;
    padding-left: 16px;
}

.menu-item {
    display: flex;
    padding: 3px 0px;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.menu-item p {
    color: var(--link-on-highlight, #FDFEFF);
    /* Label/Regular */
    font-family: "arial";
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 112.5%;
    /* 18px */
    letter-spacing: 0.08px;
}

.divider {
    width: 1px;
    height: 32px;
    background: var(--specific-overlay-on-highlight, rgba(0, 0, 0, 0.30));
}

.titulo {
    color: var(--specific-brand-header, #FCFC30);

    /* Title/X-Small */
    font-family: "arial";
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 125%;
    /* 22.5px */
}

.imagem {
    width: 36px;
    height: 36px;
}

.right-area {
    /* margin-left: auto; */
    display: flex;
    /* justify-content: flex-end; */
    align-items: center;
    justify-content: center;
    gap: 16px;
    margin-left: -100px;
    position: absolute; /* Adiciona esta linha */ 
    right: 0; /* Adiciona esta linha */ 
    transform: translateX(-50px);
}

.stroke {
    height: 4px;
    align-self: stretch;
    background: var(--specific-brand-focus, #FCFC30);
}

.container2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 1px;
}

.menu-item2 {
    display: flex;
    align-items: center;
    gap: 8px;
}

.container2 p {
    color: var(--link-on-highlight, #FDFEFF);

    /* Label/Regular */
    font-family: "arial";
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 112.5%;
    /* 18px */
    letter-spacing: 0.08px;
}


.right-menu {
    align-items: center;
    display: flex;
    gap: 16px;
}