.tcontainer {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    width: 100%;
    background-color: transparent;
}

.table-container {
    width: 100%;
    margin-top: 30px;
    padding: 25px 25px;
    /* border-radius: 12px; */
    background-color: #FFF;
    /* box-shadow: 0px 6px 8px 0px rgba(24, 24, 27, 0.06), 0px 1px 4px 0px rgba(24, 24, 27, 0.08), 0px 0px 1px 0px rgba(24, 24, 27, 0.08); */
    max-height: 60vh; /* Define a altura máxima da div contêiner */
}

.tabela {
    max-height: 100%; /* Limita a altura máxima da tabela */
    overflow-y: auto; /* Adiciona barra de rolagem vertical se necessário */
    overflow-x: auto; /* Adiciona barra de rolagem horizontal se necessário */
    max-width: 100%;
}

table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
}

thead {
    background-color: #f8f9fa;
    text-align: center;
    position: sticky; /* Torna o cabeçalho fixo */
    top: 0; /* Fixa o cabeçalho no topo */
    z-index: 1; /* Garante que o cabeçalho fique acima do corpo da tabela */
}

tbody {
    background-color: #fff;
    cursor: pointer;
}

.tbody_versoes {
    background-color: #fff;
    cursor:default;
}

th, td {
    padding: 10px;
    border-bottom: 1px solid #dee2e6;
    text-align: center;
    white-space: nowrap;
}

th {
    background-color: #f8f9fa;
    font-weight: bold;
}

td {
    font-size: 0.9em;
}

tr:hover {
    background-color: #f8f9fa;
}

.selected {
    background-color: #d6eaf8; /* Cor para a linha selecionada */
}

.selected:hover{
    background-color: #d6eaf8;
}

.date-indicator {
    font-size: 0.8em;
    color: #6c757d;
    background-color: #e9ecef;
    padding: 5px 10px;
    border-radius: 4px;
    margin-left: 10px;
}

.loading-spinner {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
    font-size: 24px;
    color: #6c757d;
    padding: 20px;
}

.error-message {
    background-color: #fff3f3;
    color: #dc3545;
    padding: 15px;
    border-radius: 4px;
    margin: 20px 0;
    text-align: center;
}

.table-link {
    color: #0066cc;
    padding: 5px 10px;
    border-radius: 4px;
    background-color: #e7f1ff;
    transition: all 0.2s ease;
    text-decoration: none;
}

.table-link:hover {
    background-color: #0066cc;
    color: white;
    text-decoration: none;
}

h1 {
    color: #333;
    margin-bottom: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
