.frame {
    display: flex;
    width: 80%;
    height: fit-content;
    padding: 48px 74px;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 37px;
    border-radius: 12px;
    background: #FFF;
    /* Shadow/Level-2 */
    box-shadow: 0px 6px 8px 0px rgba(24, 24, 27, 0.06), 0px 1px 4px 0px rgba(24, 24, 27, 0.08), 0px 0px 1px 0px rgba(24, 24, 27, 0.08);
}

.input-group {
    display: flex;
    width: 100%;
    flex-direction: column;
    gap: 8px;
    height: fit-content;
    justify-content: flex-start;
}

.input-box {
    width: 100%;
    color: var(--text-soft, #6C7077);
    /* Input/Regular */
    font-family: "arial";
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 125%;
    /* 20px */
    letter-spacing: 0.08px;
    border: 0px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: fit-content;
}



.select {
    display: flex;
    padding: 8px 16px 0px 16px;
    align-items: center;
    gap: 10px;
    align-self: stretch;
    border-radius: 4px 4px 0px 0px;
    background: var(--background-neutral-default, #F0F2F4);
    width: 20%;
    cursor: pointer;
}

.tabela {
    justify-content: flex-start;
    display: flex;
}

.options {
    background: #F0F2F4; /* Fundo neutro */
    width: 20%; /* Largura ajustada para caber melhor */
    border-radius: 4px; /* Canto arredondado */
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1); /* Sombra para dar destaque */
    margin-top: 8px; /* Espaço entre a caixa de seleção */
    padding: 8px 0; /* Espaço interno */
}

.options li {
    list-style-type: none; /* Remover bullet points */
    padding: 10px 16px; /* Espaçamento interno */
    margin: 4px 0; /* Espaço entre os itens */
    border-radius: 4px; /* Canto arredondado */
    background: #FFFFFF; /* Fundo branco */
    color: #333; /* Cor do texto */
    cursor: pointer; /* Cursor de mãozinha ao passar o mouse */
    transition: background 0.3s, transform 0.2s; /* Transições suaves */
}

.options li:hover {
    background: #E0E9FF; /* Cor de fundo ao passar o mouse */
    transform: scale(1.03); /* Leve aumento ao passar o mouse */
}


.parte-baixo {
    padding-top: 30px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.botoes {
    display: flex;
    align-items: center;
    gap: 23px;
}

.botao-salvar {
    display: flex;
    width: 97px;
    padding: 9px 16px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border-radius: 4px;
    background: var(--button-primary-default, #FCFC30);
    border: 0px;
    cursor: pointer;
}

.botao-cancelar {
    display: flex;
    padding: 9px 16px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border-radius: 4px;
    background: var(--button-secondary-default, #E0E9FF);
    border: 0px;
    cursor: pointer;
}

.botao-cancelar p,
.botao-salvar p {
    color: var(--text-on-secondary, #465EFF);
    text-align: center;

    /* Button/Label Small */
    font-family: "arial";
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: 112.5%;
    /* 13.5px */
    letter-spacing: 0.06px;
    text-transform: uppercase;
}

.input {
    background: transparent;
    border: 0px;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}


input:focus {
    outline: none;
}

.nome {
    display: flex;
    justify-content: flex-start;
    align-items: self-start;
    background-color: yellow;
    height: 200px;
}

.botao-salvar:hover {
    background-color: #e4e70a;
    /* Cor de fundo ao passar o mouse */
    transform: scale(1.05);
    /* Leve aumento ao passar o mouse */
  }

.botao-cancelar:hover {
    transform: scale(1.05);
    background-color: #949cb1;
}

.haha {
    width: fit-content;
}