body, html {
    display: flex;
    max-width: 100vw;
    width: 100%;
    padding-bottom: 81px;
    flex-direction: column;
    align-items: center;
    gap: 82px;
    background: var(--background-neutral-default, #F0F2F4);
    margin: 0px;
    margin-right: 0px;
    box-sizing: border-box;
    overflow-x: hidden;
    font-family: "arial";
}

.teste {
    display: flex;
    width: 100vw;
    padding-bottom: 81px;
    flex-direction: column;
    align-items: center;
    gap: 82px;
    justify-content: flex-start;
}